import React from 'react';
import { ButtonGroup } from 'react-bootstrap';
import { BrowserRouter as Router, Link } from 'react-router-dom';
import { crollToElemetHandler } from '../js/crollToElemetHandler';

// set up text to print, each item in array is new line
var aText = new Array(
  { text: 'Кряжев Олег', tag: 'h2' },
  { text: 'Frontend-разрабочик', tag: 'h4' },
  { text: 'Добро пожаловать на мой сайт!', tag: 'p' }
);
var iSpeed = 100; // time delay of print out
var iIndex = 0; // start printing array at this posision
var iArrLength = aText[0].text.length; // the length of the text array
var iScrollAt = 20; // start scrolling up at this many lines

var iTextPos = 0; // initialise text position
var sContents = ''; // initialise contents variable
var iRow; // initialise current row

const typewriter = () => {
  setTimeout(() => {
    sContents = ' ';
    iRow = Math.max(0, iIndex - iScrollAt);
    console.log(iIndex);
    var destination = document.getElementById(`typedtext-${aText[iIndex].tag}`);

    while (iRow < iIndex) {
      sContents += aText[iRow++].text + '<br />';
      sContents = '';
    }
    destination.innerHTML =
      sContents + aText[iIndex].text.substring(0, iTextPos);
    if (iTextPos++ == iArrLength) {
      iTextPos = 0;
      iIndex++;
      if (iIndex != aText.length) {
        iArrLength = aText[iIndex].text.length;
        typewriter();
      }
    } else {
      typewriter();
    }
  }, iSpeed);
};

class Home extends React.Component {
  componentDidMount() {
    typewriter();
  }

  render() {
    return (
      <section id="home-page" className="home">
        <div className="row">
          <div className="col-md-5 offset-md-1 d-flex align-items-center justify-content-end">
            <div className="greetings">
              <h2 id="typedtext-h2"></h2>
              <h4 id="typedtext-h4"></h4>
              <p id="typedtext-p"></p>

              <ButtonGroup id="nav-buttons-home">
                <a
                  to="/about"
                  onClick={crollToElemetHandler('about')}
                  className="btn btn-secondary mx-2"
                >
                  Навыки
                </a>

                <a
                  to="/portfolio"
                  onClick={crollToElemetHandler('portfolio')}
                  className="btn btn-secondary mx-2"
                >
                  Портфолио
                </a>

                <a
                  to="/contacts"
                  onClick={crollToElemetHandler('contacts')}
                  className="btn btn-secondary mx-2"
                >
                  Контакты
                </a>
              </ButtonGroup>
              <a
                to="/about"
                onClick={crollToElemetHandler('about')}
                className="next-page-btn"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="50"
                  height="50"
                  fill="currentColor"
                  className="bi bi-arrow-down-circle-fill"
                  viewBox="0 0 16 16"
                >
                  <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v5.793L5.354 8.146a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 10.293V4.5z" />
                </svg>
              </a>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Home;
